html {
  overflow-y: scroll;
}

a {
  text-decoration: none;
  color: var(--beige);
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
}
a:hover {
  color: var(--beige);
}

.form {
  background: var(--gray);
  padding: 40px;
  min-width: 550px;
  max-width: 550px;
  margin: 80px auto;
  border-radius: 10px;
  /* box-shadow: 0 4px 10px 4px rgba(19, 35, 47, 0.3); */
}

.tab-group {
  list-style: none;
  padding: 0;
  margin: 0 0 40px 0;
}
.tab-group:after {
  content: "";
  display: table;
  clear: both;
}
.tab-group li a {
  display: block;
  text-decoration: none;
  padding: 15px;
  background: rgba(160, 179, 176, 0.25);
  color: var(--darkgray);
  font-size: 20px;
  float: left;
  width: 50%;
  text-align: center;
  cursor: pointer;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
}
.tab-group li a:hover {
  background: var(--darkgreen);
  color: var(--darkgray);
}

.tab-group li.active a {
  background: var(--darkgreen);
  color: var(--lemon);
}

h1 {
  text-align: center;
  color: var(--darkgray);
  font-weight: 300;
  margin: 0 0 40px;
}

label {
  position: absolute;
  -webkit-transform: translateY(6px);
  transform: translateY(6px);
  left: 13px;
  color: rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  pointer-events: none;
  font-size: 16px;
}

label .req {
  margin: 2px;
  color: var(--darkgreen);
}

input::placeholder {
  font-size: 16px;
}
label.active {
  -webkit-transform: translateY(50px);
  transform: translateY(50px);
  left: 2px;
  font-size: 14px;
}
label.active .req {
  opacity: 0;
}

label.highlight {
  color: var(--darkgreen);
}

input,
textarea {
  font-size: 22px;
  display: block;
  width: 100%;
  height: 100%;
  padding: 5px 10px;
  background: none;
  background-image: none;
  border: 1px solid #a0b3b0;
  color: var(--darkgray);
  border-radius: 0;
  -webkit-transition: border-color 0.25s ease, box-shadow 0.25s ease;
  transition: border-color 0.25s ease, box-shadow 0.25s ease;
}
input:focus,
textarea:focus {
  outline: 0;
  border-color: var(--darkgreen);
}

textarea {
  border: 2px solid var(--gray);
  resize: vertical;
}

.field-wrap {
  position: relative;
  margin-bottom: 40px;
}

.top-row:after {
  content: "";
  display: table;
  clear: both;
}
.top-row > div {
  float: left;
  width: 48%;
  margin-right: 4%;
}
.top-row > div:last-child {
  margin: 0;
}

.button {
  border: 0;
  outline: none;
  border-radius: 0;
  padding: 15px 0;
  font-size: 1.3rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  background: var(--darkgreen);
  color: var(--lemon);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-appearance: none;
  appearance: none;
}
.button:hover,
.button:focus {
  background: var(--darkgreen);
}

.button-block {
  display: block;
  width: 100%;
}

.forgot {
  margin-top: -20px;
  text-align: right;
}

@media (max-width: 550px) {
  .form {
    max-width: 300px;
    min-width: 200px;
  }
}
